import React from "react";
import { Row, Col } from "antd";

const Logo = (props) => {
  return (
    <Row>
      <Col
        span={24}
        style={{
          display: "block",
          background: "url(./assets/logo_01.png) 0px",
          backgroundSize: "cover",
          width: "128px",
          height: "256px",
        }}
      ></Col>
    </Row>
  );
};

export default Logo;
