import React from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { Button, Col, Popconfirm, Row, Space, Typography } from "antd";
import * as anticons from "@ant-design/icons";
import { getLabel, labels } from "../../utils/Internationalization";

const Empresa = ({ empresa, isLoading, isDeleting, consultar, eliminar }) => {
  return (
    <>
      <Row gutter={[16, 16]}>
        <Col span={24}>
          <Row align="middle" gutter={10}>
            <Col>
              <Link to={"/empresas"}>
                <anticons.ArrowLeftOutlined />
              </Link>
            </Col>
            <Col>
              <Typography.Title
                level={4}
                style={{
                  margin: 0,
                }}
              >
                Empresa
              </Typography.Title>
            </Col>
          </Row>
        </Col>
        <Col span={24}>
          <Row justify="end">
            <Col>
              <Space>
                <Popconfirm
                  title="Seguro(a) que desea eliminar la empresa?"
                  okText="Si, eliminar"
                  cancelText="No"
                  onConfirm={eliminar}
                  okButtonProps={{
                    loading: isDeleting || isLoading,
                  }}
                >
                  <Button
                    type="primary"
                    disabled={isDeleting || isLoading}
                    danger
                    loading={isDeleting}
                    icon={<anticons.DeleteFilled />}
                  >
                    {isDeleting
                      ? getLabel(labels.deleting)
                      : getLabel(labels.delete)}
                  </Button>
                </Popconfirm>
                <Button
                  type="primary"
                  onClick={() => consultar()}
                  disabled={isLoading || isDeleting}
                >
                  {getLabel(labels.refresh)}
                </Button>
              </Space>
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
};

Empresa.propTypes = {
  empresa: PropTypes.object.isRequired,
  isLoading: PropTypes.bool.isRequired,
  isDeleting: PropTypes.bool.isRequired,
  consultar: PropTypes.func.isRequired,
  eliminar: PropTypes.func.isRequired,
};

export default Empresa;
