import React from "react";
import { useParams } from "react-router-dom";
import Empresa from "../../components/empresa/Empresa";
import usePersona from "../../hooks/usePersona";

const EmpresaPage = () => {
  const { id } = useParams();
  const empresaInicial = { perId: id };
  const { empresa, isLoading, isDeleting, consultar, eliminar } =
    usePersona(empresaInicial);
  const acciones = { consultar, eliminar };
  const propiedades = { empresa, isLoading, isDeleting };
  return <Empresa {...propiedades} {...acciones} />;
};

export default EmpresaPage;
